* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;

  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 20px;
}

.country-list__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 300px;

  border: 5px solid gold;
  border-radius: 25px;

  padding: 10px;
}

h2 {
  text-align: center;
}

.country-info {
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  text-align: center;
  width: 500px;
  border: 5px solid gold;
  border-radius: 25px;
}

p {
  font-size: 30px;
}

.country-name {
  font-size: 50px;
}
